  0% {
    transform: translateY(0px);
  }
  50% {
    transform: translateY(-3px);
  }
  100% {
    transform: translateY(0px);
  }
}
@keyframes opacity {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}
@keyframes opacity-reverse {
  0% {
    opacity: 1;
  }
  100% {
    opacity: 0;
  }
}
@keyframes hover-up-down-3D {
  0% {
    transform: translateY(0px);
  }
  50% {
    transform: translateY(-3.5px);
  }
  100% {
    transform: translateY(0px);
  }
}
@keyframes navLinkBlueShadow {
  0% {
    box-shadow: 0 0 0px #287aff;
  }
  50% {
    box-shadow: 0 0 8px #287aff;
    text-shadow: 0px 0px 10px #287aff;
  }
  100% {
    box-shadow: 0 0 0px #287aff;
  }
}
@keyframes slide-in-left {
  0% {
    transform: translateX(-100%);
  }
  100% {
    transform: translateX(0%);
  }
}
.inlineOrangeText {
  color: #f4a227;
  text-shadow: 2px 2px 2px black;
  display: inline;
}
.inlineBlueText {
  color: #24b0df;
  text-shadow: 2px 2px 2px black;
  display: inline;
}
.inlineRedText {
  color: #db3523;
  text-shadow: 2px 2px 2px black;
  display: inline;
}
.inlineWhiteText {
  color: #ededed;
  text-shadow: 2px 2px 2px black;
  display: inline;
}
.draggableContainer {
  position: relative;
  width: 100vw;
  height: 100vh;
  overflow: hidden;
  border-radius: 0px;
  transition: all 0.5s ease-in-out;
  background-size: 1.6em 1.6em;
  background-attachment: local;
  background-image: linear-gradient(rgba(0, 0, 0, 0.5) 0.1em, transparent 0.1em), linear-gradient(90deg, rgba(0, 0, 0, 0.5) 0.1em, transparent 0.1em);
}
.graph {
  position: relative;
  display: flex;
  height: 100vh;
  transition: all 0.5s ease-in-out;
  backdrop-filter: blur(0.2rem);
  border-radius: 0 0 20px 20px;
  animation: opacity 1s ease-in-out;
  background-size: 1.5em 1.5em;
  background-attachment: local;
  overflow: hidden;
  padding: 0;
  cursor: grab;
}
.graph:active {
  cursor: grabbing;
}
.graph.dark {
  background-color: rgba(0, 0, 0, 0.2);
  border: 1px solid rgba(0, 0, 0, 0.2);
  border-top: none;
  background-image: linear-gradient(rgba(255, 255, 255, 0.1) 0.1em, transparent 0.1em), linear-gradient(90deg, rgba(255, 255, 255, 0.1) 0.1em, transparent 0.1em);
}
.graph.light {
  background-color: rgba(255, 255, 255, 0);
  border: 1px solid rgb(255, 255, 255);
  border-top: none;
  background-image: linear-gradient(rgba(0, 0, 0, 0.4) 0.1em, transparent 0.1em), linear-gradient(90deg, rgba(0, 0, 0, 0.4) 0.1em, transparent 0.1em);
}
.row {
  perspective: 1000px;
}
.draggableInner {
  transform-origin: center center;
  position: relative;
  height: fit-content;
  width: fit-content;
  transition: all 0.5s ease-in-out;
  scale: 1;
}
.draggableContent {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  position: relative;
  min-height: 100vh;
  transition: all 0.5s ease-in-out;
  transform-origin: 0% 0%;
  overflow: hidden;
  scale: 1;
}
@keyframes title-hover {
  0% {
    transform: translate(-50%, 0px);
  }
  50% {
    transform: translate(-50%, 8px);
  }
  100% {
    transform: translate(-50%, 0px);
  }
}
.node-graph-title-bar {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  position: relative;
  width: 100%;
  height: 4rem;
  border-radius: 20px 20px 0 0;
  color: #ededed;
  padding: 1rem;
  transition: all 0.2s ease-in-out;
  animation: opacity 1s ease-in-out;
  backdrop-filter: blur(0.2rem);
  font-size: 3rem;
  text-decoration: underline;
}
.node-graph-title-bar .node-graph-title {
  position: absolute;
  left: 50%;
  transform: translate(-50%, -50%);
  background-color: transparent;
  animation: title-hover 4s infinite ease-in-out;
}
.node-graph-title-bar.dark {
  background-color: rgba(0, 0, 0, 0.2);
  color: white;
  border: 1px solid rgba(0, 0, 0, 0.2);
}
.node-graph-title-bar.light {
  background-color: rgba(255, 255, 255, 0);
  color: black;
  border: 1px solid rgb(255, 255, 255);
