  0% {
    transform: translateY(0px);
  }
  50% {
    transform: translateY(-3px);
  }
  100% {
    transform: translateY(0px);
  }
}
@keyframes opacity {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}
@keyframes opacity-reverse {
  0% {
    opacity: 1;
  }
  100% {
    opacity: 0;
  }
}
@keyframes hover-up-down-3D {
  0% {
    transform: translateY(0px);
  }
  50% {
    transform: translateY(-3.5px);
  }
  100% {
    transform: translateY(0px);
  }
}
@keyframes navLinkBlueShadow {
  0% {
    box-shadow: 0 0 0px #287aff;
  }
  50% {
    box-shadow: 0 0 8px #287aff;
    text-shadow: 0px 0px 10px #287aff;
  }
  100% {
    box-shadow: 0 0 0px #287aff;
  }
}
@keyframes slide-in-left {
  0% {
    transform: translateX(-100%);
  }
  100% {
    transform: translateX(0%);
  }
}
.inlineOrangeText {
  color: #f4a227;
  text-shadow: 2px 2px 2px black;
  display: inline;
}
.inlineBlueText {
  color: #24b0df;
  text-shadow: 2px 2px 2px black;
  display: inline;
}
.inlineRedText {
  color: #db3523;
  text-shadow: 2px 2px 2px black;
  display: inline;
}
.inlineWhiteText {
  color: #ededed;
  text-shadow: 2px 2px 2px black;
  display: inline;
}
.switchContainer {
  position: relative;
  margin: 0.1em;
  border-radius: 20px;
  background-color: #0e2b58;
  padding: 0.2em;
}
.switchContainer::after {
  content: "";
  position: absolute;
  width: 1px;
  height: 100%;
  background-color: rgba(255, 255, 255, 0.05);
  left: 50%;
  top: 0;
}
.switchButton {
  position: relative;
  display: flex;
  align-items: center;
  justify-content: center;
}
.check-box {
  position: relative;
  display: flex;
  align-items: center;
  justify-content: center;
}
.brightnessMode {
  position: absolute;
  appearance: none;
  width: 100%;
  height: 100%;
  border-radius: 50%;
  cursor: pointer;
  transition: 0.4s;
}
.brightnessMode:checked + .sunIcon, .brightnessMode:checked + .moonIcon {
  background-color: #236bdc;
  border-radius: 50%;
  padding: 0.1em;
  margin: 0.05em;
}
.sunIcon {
  width: 1.5em;
  height: 1.5em;
  transition: 0.2s ease-in-out;
  pointer-events: none;
}
.moonIcon {
  width: 1.5em;
  height: 1.5em;
  transition: 0.2s ease-in-out;
  pointer-events: none;
}
.modeLabel {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  position: relative;
  margin: 0 0.5em;
}
.slider {
  position: absolute;
  width: 2em;
  height: 2em;
  top: -0.3em;
  left: -0.5em;
  background: transparent;
  transition: 0.4s ease-in-out;
  pointer-events: none;
}
.switchBar[type=checkbox]:checked + .slider {
  left: 50%;
  width: 2em;
  height: 2em;
  top: -0.3em;
}
.themeIcon {
  position: absolute;
  width: 100%;
  height: 100%;
  transition: 0.2s ease-in-out;
  pointer-events: none;
